}
/**/
header {
    position: relative;
    text-align: center;
    margin-bottom: 20px;
}
header>h1 {
    position: relative;
    display: inline;
    padding: 0 1em;
    background: #fff;
    z-index: 9;
}
header:after {        /**/
    content: '';
    width: 100%;
    height: 0;
    position: absolute;
    top: 50%;
    left: 0;
    border: .5px solid #ccc;
}
/**/
#teams-wrap {
    width: 45%;
    float: left;
}
.continent {
    margin-bottom: 5px;
}
.continent>h2 {
    font: 16px/2 'Hiragino GB' bold;
}
.team {
    margin: 0 10px 5px 0;
    display: inline-block;
}
.team a {
    display: block;
    text-align: center;
    color: #000;
}
.team img {
    border: 1px solid #000;
}

/**/
#players-wrap {
    margin: 0 0 10px 50%;
}
</style>


