<!-- 
    http://localhost:8080/teams/teamTable/:id 
-->
<!--  VS data-->
<!-- Id VS v-forindex-->
<!-- 2.10: propsteamsPage -->
<!-- beforeRouteUpdate -->
<!-- 3.5 XXX: propsidplayerId
3.6 propsteamsteamListajaxteams.currentTeamundefiendvue-devtoolsHTMLv-ifpropsdefault -->
<template>
    <div id="players-data">
        <aside class="search">
            <h2></h2>
                <div class="search-content">
                    <div class="searchRow">
                        <label for="name"></label> 
                        <input type="text" placeholder="" v-model="searchName">
                    </div>
                    <div class="searchRow">
                        <label for="role"></label>
                        <select id="role" required v-model="searchRole">
                            <option disabled value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                            <option value=""></option>
                        </select>
                    </div>
                    <div class="searchRow">
                        <label for="club"></label>
                        <input id="club" type="text" placeholder="" v-model="searchClub">
                    </div>
                </div>
        </aside>
        
        <section class="table-team">
            <!-- v-for+v-if="currentId" -->
            <!-- +id -->
            <!-- teams.currentTeamcurrentTeamundefiend -->
            <h3 class="table-caption" v-if="currentTeam">
                <p>{{ currentTeam.teamName }}</p>
                <!--  -->
                <router-link class="add-player playerBtn" to="addPlayer" append tag="button">      
                </router-link>
                <!-- <router-link :to="'/teams/teamTable/' + team.id + '/addPlayer'"></router-link> -->
            </h3>
            <!-- title -->
            <ul class="table-title">
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
                <li></li>
            </ul>
            <!-- -->
            <!-- filterPlayersv-if -->
            <ul class="table-player" v-if="filterPlayers.length" v-for="keyPlayer in filterPlayers">
                <li>{{keyPlayer.name}}</li>
                <li>{{keyPlayer.number}}</li>
                <li>{{keyPlayer.role}}</li>
                <li>{{keyPlayer.age}}</li>
                <li>{{keyPlayer.club}}</li>
                <router-link :to="'playerData/' + keyPlayer.playerId" append tag="li">
                    <button class="playerBtn"></button>
                </router-link>
                <!-- append, toparamsparams -->
                <!-- 
                    <router-link tag="li" 
                        :to="{
                               name:'playerData',
                               params:{playerId: keyPlayer.playId}
                            }">
                    </router-link></li> 
                -->
            </ul>
        </section>

        <footer v-if="alert">{{ alert }}</footer>
    </div>
</template>

<script>
export default {
    name: 'team-data',
    props: {
        teams: Array,
        teamId: String,
        playerId: String
    },
    data() {
        return {
            // currentTeam: this.teams[this.$route.params.id],props{teamId: route.params.id}
            alert: '',
            searchName: '',
            searchRole: '',
            searchClub: '', 
        }
    },
    computed: {
        // XXXcurrentTeamdata
        currentTeam() {
            return this.teams[this.teamId];
        },
        // ,true
        // forfilter; 
        // XXX &&
        // XXX push()result[i]=keyPlayers[i],undefined
        // XXX indexOf
        filterPlayers() {
            // propscurrentTeamundefiend
            // keyPlayer
            // 
            if(this.currentTeam && Array.isArray(this.currentTeam.keyPlayers)) {
                let keyPlayers = this.currentTeam.keyPlayers;
                let result = [];
                for(let i = keyPlayers.length - 1; i>=0; i--) {
                    // 
                    let nameFlag = this.searchName === ''
                    ?true
                    :(keyPlayers[i].name.indexOf(this.searchName) !== -1
                      ?true:false);
                    // 
                    let roleFlag = this.searchRole === ''
                    ?true
                    :(keyPlayers[i].role.indexOf(this.searchRole) !== -1
                      ?true:false);
                    // 
                    let clubFlag = this.searchClub === ''
                    ?true
                    :(keyPlayers[i].club.indexOf(this.searchClub) !== -1
                      ?true:false);
                    if(nameFlag && (roleFlag && clubFlag)) {
                        result.push(keyPlayers[i]);
                    }
                }
                return result;
            }
        }
    },

    mounted() {
        this.$nextTick(function () {
            // mounted
            // beforeRouteUpdate
            // mounted$route.query
            // teamsPage.vue
            this.alert = this.$route.query.alert;
        });
    },
    // this.$route.query.alertwatchalert
    // this.$route.query.alertbeforeRouteEnter,thi
    // updatedDOM
    // alertcreatedmounted
    updated() {
    },

    // keep-aliveactivated
    activated() {
    // this.fetchTeam(this.$route.params.id);
    },

    // 
    //  /foo/:id
    //  /foo/1  /foo/2 watch $route
    beforeRouteUpdate(to,from,next) {
        // this.currentTeam = this.teams[to.params.id];  currentTeam
        // alert
        this.alert = '';
        next();
    },
    // watch
    // watch: {
    //     $route(to) {
    //         this.fetchTeam(to.params.id);
    //     }
    // },
    
    // next()
    beforeRouteLeave(to,from,next) {
        this.alert = '';
        next();
    }
}
</script>

<style>
/**/
.search-content {
    display: flex;
    justify-content: space-between;
}
.searchRow>* {
    padding: 3px 0;
    font-size: 14px;
}

/**/
.table-team {
    display: table;
    width: 600px;
    margin: 20px auto;
    font: 14px/2 "Hiragino GB";
    text-align: center;
    border: 1px solid #0041c2;
}
.table-team>h3 {
    display: table-caption;
    text-align: center;
    font-size: 16px;
}
.table-caption {
    position: relative;
}
.table-title,
.table-player {
    display: table-row;
}
.table-title>li,
.table-player>li {
    display: table-cell;
}
.table-title {
    background: #0041c2;
    color: #fff;
}
.table-player:nth-child(even) {
    background: #ebeff4;
}

/**/
.add-player {
    position: absolute;
    bottom: 1px;
    right: 0;
}
.playerBtn {
    padding: 2px;
    font-size: 12px;
    background: #9f000f;
    border: none;
    color: #fff;
}
</style>








