<!-- 
    http://localhost:8080/teams/teamTable/:id/playerData/:playerId
-->
<!-- axios.getplayer -->
<!-- this.$route.params.idid{string}Number -->
<!-- propsJSON.parse(Json.stringify) -->

<!-- 3-7 XXX: HTMLv-if-->
<!-- HTMLundefined -->
<!-- XXX: team -->
<template>
	<div class="playerData">
        <div class="content">
            <!-- teamsHTMLteam -->
            <!-- team -->
            <!--  -->
            <h2 v-if="currentTeam.keyPlayers">{{ player.name }}</h2>
            <dl>
                <dt>{{ infoRefer.nation }}</dt>
                <dd>{{ currentTeam.teamName }}</dd>
            </dl>
            <!-- v-ifv-forv-if templateif-->
            <template v-if="currentTeam.keyPlayers">
                <dl 
                    v-for="(value,key) in player"
                    v-if="key !== 'playerId' && key !== 'name'">
                    <dt>{{ infoRefer[key] }}</dt>
                    <dd>{{ value }}</dd>
                </dl>
            </template>

            <!--  -->
            <router-link class="btn editBtn" to="edit" tag="button" append>
                
            </router-link>
            <!--  -->
            <button class="btn deleteBtn" @click="isShowDialog=true">
                
            </button>
            <!--  -->
            <router-link class="back" :to="`/teams/teamTable/${this.currentId}`">
                &lt;&lt;{{ currentTeam.teamName }}
            </router-link>
        </div>
        <!--  -->
        <dialog-delete 
            v-if="isShowDialog" 
            :isShow="isShowDialog" 
            @confirmDel="deletePlayer" 
            @cancelDel="isShowDialog=false">
        </dialog-delete>
	</div>
</template>

<script>
import dialogDelete from "../base/dialog-delete"
export default {
	name: 'playerData',
    components: {
        dialogDelete
    },
    props: {
        teams: [Array]
    },
	data() {
		return {
            infoRefer: {
                'name': '',
                'nation': '',
                'club': '',
                'age': '',
                'height': '',
                'number': '',
                'role': '',
            },
            // @value {String}currentId,currentPlayerId
            currentId: this.$route.params.id,
            currentPlayerId: this.$route.params.playerId,
            isShowDialog: false
		}
	},
    computed: {
        currentTeam() {
            return this.teams[this.currentId];
        },
        player() {
            return this.currentTeam.keyPlayers[this.currentPlayerId];
        }    
    },
    methods: {
        // 
        deletePlayer() {
            // 
            this.currentTeam.keyPlayers.splice(this.currentPlayerId, 1);
            this.$emit('delete', {id: this.currentId, team: this.currentTeam});
        }
    }
} 
</script>

<style scoped>
.content {
    padding-top: 10px;
}
dl {
    display: flex;
}
dt,dd {
    margin: 5px 10px 5px 0;
    width: 100px;
    font: 14px/1.5 'FangSong' bold;
    cursor: pointer;
}
dd {
    background: #9172ec;
    color: #fff;
    text-align: center;
}
.btn {
    width: 80px;
    margin: 10px 10px 5px 0;
    padding: 3px 5px;
    border: none;
    outline-width: 0;
    color: #fff;
}
.editBtn {
    background: #d9534f;
}
.deleteBtn {
    background: #306eff;
}
.back {
    display: block;
}
</style>


