<template>  
    <div id="draw">
        <!--  -->
        <draw-dialog :isShow="isShowDialog" v-if="curRound === 0" @next="gotoNext">
            <img class="dialog-logo" slot="image" :src="logo.url">
            <p slot="text"></p>
            <span slot="buttonTxt"></span>
        </draw-dialog>
        <!--  -->
        <draw-dialog :isShow="isShowDialog" v-if="curRound > 0" @next="gotoNext">
            <img class="dialog-team" slot="image" :src="curTeam.flagUrl">
            <p slot="text">{{ curTeam.teamName }} - {{ curPos }}</p>
            <span slot="buttonTxt"></span>
        </draw-dialog>
        
        <!--  -->
        <div id="draw-notice">
            <!--  -->
            <div id="status">
                <button class="statusBtn" v-if="curRound === 0" @click.once="start">
                    START
                </button>
                <button class="statusBtn" v-else-if="curRound === -1" @click.once="createResult">
                    
                </button>         
                <div class="statusBtn" v-else @click="return false">
                    <span></span>
                    <!--  -->
                    <span v-if="curPot > 0">
                        <strong>{{ curPot }}</strong>
                        <strong>{{ orderGroupName }}</strong>
                    </span>
                </div>
            </div>
            <!--  -->
            <div class="step" v-if="curRound >= 0">
                <i class="iconfont icon-pointer-right"></i>
                {{ step }}
            </div>
        </div>

        <!--  -->
        <section id="main">
            <div id="live">
                <!--  -->
                <team-Area
                    ref="r1" 
                    @choose="setCurTeam" 
                    :curPot="curPot" 
                    :curGroupNum="curGroupNum" 
                    :drawTeamFlag="drawTeamFlag">
                </team-Area>
                <!--  -->
                <group-Area 
                    ref="r2" 
                    @choose="setCurPos" 
                    :curPot="curPot" 
                    :curGroupNum="curGroupNum" 
                    :drawPosFlag="drawPosFlag">
                </group-Area>
            </div>
            <!--  -->
            <aside id="teams-status">
                <draw-show
                    :teams="teamList"
                    :curPot="curPot"
                    :groupContainer="groupContainer"
                    :curTeamName="curTeam.teamName">
                </draw-show>
            </aside>   
        </section>
        <!-- id="main" -->
        
        <!-- -->
        <footer class="result">
            <grouping-result 
                :groupContainer="groupContainer"
                :curGroupNum="curGroupNum">
            </grouping-result>
        </footer>
    </div>
</template>

