    computed: {
        groupContainer() {
            let cont = [];
            for(let i = 0; i < 8; i ++) {
                cont[i] = this.groups.slice(i*4,i*4+4);
            }
            return cont;
        },
        curPot() {
            // 
            // 8
            switch (true) {
                case this.curRound > 24:
                    return 4;
                    break;

                case this.curRound > 16 && this.curRound <= 24:
                    return 3;
                    break;

                case this.curRound > 8 && this.curRound <= 16:
                    return 2;
                    break;

                case this.curRound > 0 && this.curRound <= 8:
                    return 1;
                    break;

                default:
                    return 0;
                    break;
            }
        },
        // <
        // 
        orderGroupNum() {
            let groupIdx = 1;
            for(let i = 1; i <= 8; i++) {
                let occupy = this.groupContainer[i-1]
                    .filter(item => item.team);
                if (occupy.length < this.curPot) {
                    break;
                    // XXX: continuebreak
                }
                groupIdx++; 
            }
            // 81
            return groupIdx === 9?1:groupIdx;
        },
        // : fromCharCode
        orderGroupName() {
            return String.fromCharCode(this.orderGroupNum+64);
        },
        curGroupName() {
            return String.fromCharCode(this.curGroupNum+64);
        }
    },


    methods: {   
        gotoNext() {
            if(this.curRound === 0) {
                this.isShowDialog = false;
            }
            else if(this.curRound > 0) {
                this.isShowDialog = false;
                this.enterGroup();
            }
        },

        // 
        start() {
            for(let i = 0; i < 4; i++) {
                this.$refs.r1.shuffleOrder(i);
            }            
            for(let i = 0; i < 8; i++) {
                this.$refs.r2.shuffleGroup(i);
            }
            this.curRound = 1;
            this.step = "";
            this.drawTeamFlag = true;
            this.isShowDialog = false;
        },


        /**
         * 
         *  from teamArea.vue
         * @event click
         * @param {Object} team teamArea.vueteam
         */
        setCurTeam(selectedTeam) {
            // 
            this.curTeam = selectedTeam;
            this.curGroupNum = this.orderGroupNum;
            // 
            if(this.curPot === 1) {
                this.diffAlert(selectedTeam,this.orderGroupNum,this.curGroupNum);
            }
            // 
            else if(selectedTeam.continent !== '') {
                this.avoidNonEuro(selectedTeam);
            }
            // 
            else {
               this.avoidEuro(selectedTeam);
            }
            this.drawTeamFlag = false;
            this.drawPosFlag = true;
        },
        // 
        avoidNonEuro(selectedTeam) {
            // 
            while(this.curGroupNum <= 8) {
                let group = this.groupContainer[this.curGroupNum - 1];
                let teamsLen = group.filter(item => item.team).length;
                let isEncounter = group.some(item => item.team 
                    && item.team.continent === selectedTeam.continent);
                // curGroup
                if(teamsLen < this.curPot && !isEncounter) {
                    this.diffAlert(selectedTeam,this.orderGroupNum,this.curGroupNum);
                    break;
                }
                else {
                    this.curGroupNum++;
                }
            }
        },
        // 
        avoidEuro(selectedTeam) {
            // 
            while(this.curGroupNum <= 8) {  
                let group = this.groupContainer[this.curGroupNum - 1];
                let teamsLen = group.filter(item => item.team).length;
                let euroTeams = group.filter(item => item.team 
                    && item.team.continent === "");
                // 
                if(teamsLen < this.curPot && euroTeams.length < 2) {
                    this.diffAlert(selectedTeam,this.orderGroupNum,this.curGroupNum);
                    break;
                }
                else {
                    this.curGroupNum++;
                }
            }
        },
        // 
        diffAlert(team,cur,seq) {
            if(cur === seq) {
                this.step = `
                    ${this.curTeam.teamName}${this.curGroupName}
                    ${this.curGroupName}`;
            }
            else if(team.continent !== ""){
                this.step = `
                    
                    ${this.curTeam.teamName}${this.curGroupName}
                    ${this.curGroupName}`;
            }
            else {
                this.step = `
                    
                    ${this.curTeam.teamName}${this.curGroupName}
                    ${this.curGroupName}`;
            }
        },


        /**
         * 
         *  from groupArea.vue
         * @event click
         * @param {Object} team groupArea.vuepos
         */
        setCurPos(pos) {
            this.curPos = pos.num;
            this.step = `
                ${this.curTeam.teamName}${this.curPos}`;
            this.drawPosFlag = false;
            this.isShowDialog = true;
        },

        // 
        // 
        enterGroup() {
            let position = this.curPos.slice(1);
            // 
            this.$set(this.groupContainer[this.curGroupNum - 1][position - 1], 'team', this.curTeam);
            // 1
            if(this.curRound < 32) {
                this.curRound ++;
            } else {
                // -1
                this.curRound = -1;
            }
            this.step = `${this.curPot}`;
            this.drawTeamFlag = true;
        },

        // 
        createResult() {      
            let len = this.groupContainer.length;
            for(let i = 0; i < len; i++) {
                this.$axios.post(
                    `http://localhost:3000/result`,
                    Qs.stringify(this.groupContainer[i]),
                    {
                        headers: {
                            'Content-Type': 'application/x-www-form-urlencoded'
                        }
                    })
                .then(res => {
                    console.log(res);
                })
            }
        }
    },


    mounted() {
        this.$nextTick(function () {
            this.isShowDialog = true;
        });
    }
} 
</script>

<style scoped>
/**/
#draw {
    position: relative;
    border: 2px solid #0020c2;
}
#main {
    display: flex;
}
#live {
    flex: 1;
    display: flex;
}
#teams-status {
    flex: 0 0 200px;
    order: -1;
}

/* live */
#team-area {
    flex: 55%;
}
#group-area {
    flex: 45%;
}


/*#notice*/
#draw-notice {
    position: absolute;
    width: 100%;     /**/
    bottom: -40px;  /**/
    text-align: center;
}
#status {
    /*float: left.step*/
    position: absolute; 
    left: 0;
}
.statusBtn,
.step {
    border: 1px solid #000;
    line-height: 30px;
}
/**/
.statusBtn {
    min-width: 150px;    /*min-width*/
    padding: 0 20px;
    height: 30px;
    background: #f0f8ff;
    border-radius: 8px;
    font-family: 'Hiragino GB';
    font-weight: 700;
    outline-width: 0;
}
button.statusBtn:hover {
    background: #e67451;
    color: #fff;
}
/**/
.step {
    display: inline-block;
    min-width: 280px;   /*min-width*/
    min-height: 30px;
    padding: 0 20px;
    background: #ccfb5d;
    font-size: 16px;
    font-weight: 700;
    align-self: center;
}

/*drawPage.vuedrawDialog.vue*/
.dialog-logo {
    width: 150px;
}
.dialog-team {
    width: 100px;
    border: 1px solid #000;
}


/* Color Variables

@colordef #6960EC;hover
@colordef #2B65EC;Ocean Blue
@colordef #1589FF;dodger Blue()
@colordef #FFC;cream()
@colordef #F5F5F5;white-smoke(tab)
@colordef #FEFCFF;Milk White
@colordef #e7e8e1;www.bbc.com
@colordef #f0f8ff;AliceBlue(btn)
@colordef #c2dfff;seaBlue
*/
</style>


