<!-- 1.25
    dialogcss 
-->
<!-- 1.26 
	.box-top span:hover 
	dialogprops
-->
<!-- 1.31 
	
-->
<template>
<transition name="dialog">
	<div id="dialog-delete" v-if="isShow">
	    <!--  topleft -->
		<div id="shade" @click="cancel">
		</div>
	    <!--  -->	
		<div id="dialog-delete-box">
			<!-- 	 -->
			<div class="box-top">
			    <span @click="cancel">&times;</span>
			</div>
			<div class="box-content">
			    <p>?</p>
			    <div class="btn-wrap">
			        <!-- 	 -->
			        <button class="btn-confirm" @click="confirm">Yes</button>
			        <!-- 	 -->
			        <button class="btn-cancel" @click="cancel">No</button>
			    </div>
			</div>
		</div>
	</div>
</transition>
</template>

<script>
export default {
	name:"dialog-delete",
	props: {
		isShow: {
			type: Boolean,
			dafault: false
		}
	},
	methods:{
		// 
		confirm() {
			this.$emit("confirmDel")
		},
		// (dialog)
		cancel() {
			this.$emit("cancelDel")
		}			
	}
}
</script>

<style scoped>
/*  topleft*/
.dialog-enter-active,
.dialog-leave-active {
	transition: all 0.5s;
}
.dialog-enter,
.dialog-leave-to {
	opacity: 0;
}
#shade {
	position: fixed;
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	opacity: .5;
	background: #bbb;
	z-index: 99;
}
/*  topleft*/
#dialog-delete-box {
	position: fixed;
	width: 50%;
	max-height: 50%;        /*50% */  /*margin100%*/
	top: 20%;
	left: 50%;
	overflow: auto;
	margin-left: -25%;       /**/
	background: #fefcff;
	z-index:999;
}
.box-top {
	height: 40px;
	margin-bottom: 40px;
}
.box-top span {
	position: absolute;
	right: 0;
	top: 0;
	padding: 0 5px;
	font-size: 22px;
	cursor: pointer;
}
.box-top span:hover {
    background: #306eff;	
}
.box-content {
	text-align: center;
	font-size: 16px;
}
.btn-wrap {
	margin: 50px 0 30px 0;
}
.btn-wrap button {
	padding: 10px 0;
	width: 200px;
	font-size: 18px;
	cursor: pointer;
}
.btn-confirm {
	margin: 0 5px;
	border: 1px solid #306eff;
	background: #fff;
}
.btn-cancel {
	margin: 0 5px;
	border: none;
	background: #306eff;
	color: #fff;
}
</style>


