<!-- 
    http://localhost:8080/teams/teamTable/:id/playerData/:playerId/edit 
-->
<!-- copyaddPlayer.vue -->
<!-- todo: addPlayercss-->
<!-- 3-7 XXX: team -->
<template>
	<div class="edit">
		<h4 class="error-text" v-if="errorText">{{ errorText }}</h4>
		<!-- .prevent -->
		<form v-on:submit.prevent='editPlayer'>
			<fieldset class="player-info">
				<legend></legend>
				<!--  -->
				<div class="info-row">
					<label for="name"></label>
				    <input id="name" type="text" disabled v-model="playerInfo.name">
				</div>
				<!--  -->				
				<div class="info-row">
					<label for="number"></label>
				    <input id="number" type="number" required v-model.number="playerInfo.number">
				</div>
				<!--  -->				
				<div class="info-row">
					<label for="age"></label>
				    <input id="age" type="number" required v-model="playerInfo.age">
				</div>
				<!--  -->				
				<div class="info-row">
					<label for="role"></label>
				    <select id="role" required v-model="playerInfo.role">
                        <option disabled value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
				    </select>
				</div>
				<!--  -->				
				<div class="info-row">
					<label for="club"></label>
				    <input id="club" type="text" required v-model="playerInfo.club">
				</div>
				<!--  -->				
				<div class="info-row">
					<label for="birthDate"></label>
				    <input id="birthDate" type="date" v-model="playerInfo.birthDate">
				</div>
				<!--  -->
				<div class="info-row">
					<label for="height">(cm)</label>
				    <input id="height" type="text" v-model="playerInfo.height" 
				        @blur="heightErrorFlag=true">
				    <span v-if="heightErrorFlag === true">{{ heightError.errorText }}</span>
				</div>
				<!--  -->					
				<div class="info-row">
					<label for="intro"></label>
				    <textarea id="intro" rows="10" cols="50" v-model="playerInfo.intro"></textarea>
				</div>
				<div class="info-row">
					<label for="button"></label>
				  	<button type="submit"></button>
				    <router-link 
					    class="back"
					    :to="{name:'playerData', params:{playerId:currentPlayerId}}" 
					    tag="button">
				    </router-link>
			    </div>				
			</fieldset>
		</form>
	</div>
</template>

<script>
export default {
	name: 'editPlayer',
	props: {
		teams: [Array]
	},
	data() {
		return {
            currentId: this.$route.params.id,
            currentPlayerId: this.$route.params.playerId,
            heightErrorFlag: false,
            errorText: ''
		}
	},
	computed: {
        heightError() {
            let item = this.teams[this.currentId].keyPlayers[this.currentPlayerId].height;
            let reg = /^\d{3}$/;
            let text = '';
            return this.inputError(item,reg,text);
        },
        currentTeam() {
        	return this.teams[this.currentId];
        },
        playerInfo() {
        	return this.currentTeam.keyPlayers[this.currentPlayerId];
        }
    },	
	methods: {	
		editPlayer() {
            // currentTeam
            if(this.heightError.status) {
	            this.$emit('edit', {id: this.currentId, team: this.currentTeam});
	        }
	        else {
	        	// htmlrequired
	        	this.errorText = '';
	        }    
        },
        inputError(item,reg,text) {
            let status =false;
            let errorText = '';
            if(!reg.test(item)) {
            	status = false;
            	errorText = text;
            }
            else {
                status = true;
                errorText = '';
            }
            return {
            	status,
            	errorText
            }
		}        
	}
} 
</script>

<style scoped>
.edit {
    width: 700px;
	margin: 0 auto;
}
.player-info {
	padding: 20px 0;
	background: #3d3c3a;
	border: none;
	color: #fff;
}
legend {
	margin-bottom: 10px;
	text-align: center;
	font-size: 16px;
	transform: translateY(70%);
	color: #ff0;
}
.info-row {
	margin-bottom: 15px;
	font: 14px/1.5 "STXiHei";
}
.info-row>label,
.info-row>input,
.info-row>select {
	height: 25px;
	line-height: 25px;
	width: 150px;
	vertical-align: top;
}
.info-row>label {
	width: 100px;
	padding-right: 20px;
	text-align: right;
	color: #ff0;
}
.info-row button {
	width: 50px;
	margin-right: 20px;
	padding: 3px 5px;
	border: none;
	outline-width: 0;
	color: #fff;
}
button[type='submit'] {
	background: #d9534f;
}
button.back {
	background: #306eff;
}

/**/
.format-warn {
	color: #d9543f;
	padding: 0 10px;
}
</style>


