
<!-- 
    http://localhost:8000/draw
    component: draw-show
-->
<!-- dataprops -->
<!-- 
    
    transform: scaleXz-indexES6...
    
    XXX: bug
-->
<template>
    <div id="draw-show">
        <transition-group name="pot" tag="div" class="teamsContainer" v-if="curPot">
            <!--  -->
            <div 
                class="individual-pot"
                v-for="(pot,index) in potTeams"
                v-if="index === curPot - 1"
                :key="index">
                <h2 v-if="pot.length">{{ `${pot[0].pot}` }}</h2>
                <ul class = 'teams'>
                    <!--  -->
                    <li 
                    :class="['team', {'drew': drewTeams.indexOf(team.teamName) !== -1}]"
                    v-for="team in pot" 
                    :key="team.id">
                        <img :src="team.flagUrl">
                        <div class="teamName">{{ team.teamName }}</div>
                        <!--  -->
                        <transition name="frame">
                            <div class="frame" v-if="team.teamName === curTeamName">
                            </div>
                        </transition>
                    </li>
                </ul>
            </div>
        </transition-group>
        <!--  -->
        <div class="pre-draw" v-else>
            <h2 v-for='n in 4'>{{ `${n}` }}</h2>
        </div>
    </div>
</template>

<script>
export default {
    name: 'draw-show',
    props: {
        teams: Array,
        curPot: Number,
        curTeamName: String, 
        groupContainer: Array,
    },
    computed: {
        // teams
        potTeams() {
            let pot = [];
            for(let i = 0; i < 4; i++) {
                pot.push(this.teams.slice(8*i, 8*i+8));
            }
            return pot;
        },
        // datadataprops
        drewTeams() {
            let results = [];
            // ES6
            let places = [].concat(...this.groupContainer);
            // apply(),ES5
            // [].concat.apply([],this.groupContainer); 
            // XXX: length - 1
            for(let i = places.length - 1; i >= 0; i--) {
                if(typeof places[i].team !== 'undefined') {
                    results.push(places[i].team.teamName);
                }
            }
            return results;
        }
        // curTeamName,
        // selectedTeam() {
        //    return this.curTeamName;
        // }
    }
    // watchsetter
    // watch: {
    //     selectedTeam(val) {
    //         this.drewTeams.push(val);
    //     }
    // }
}
</script>

<style scoped>
.pot-enter-active,
.pot-leave-active {
    transition: all 1s;
    /*positonabsolute*/
    position: absolute;
}
.pot-enter {
    opacity: 0;
    transform: translateX(150px);
}
.pot-leave-to {
    opacity: 0;
    transform: translateX(-150px);
}

#draw-show {
    height: 100%;
    color: #fff;
    background: #5471b0;
    border-right: 3px solid #ede275;
    /*.teamsContainerpositon: absolute; #draw-show*/
    position: relative;   
}
.teamsContainer {
    display: flex;
    text-align: center;
}
/**/
.pre-draw {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    text-align: center;
    line-height: 4;
}
.current {
    position: absolute;
    bottom: 20px;
}

/**/
.individual-pot {
    flex: none;
    width: 100%;
}

/*list*/
.teams {
    display: flex;
    flex-direction: column;
    margin-top: 15px;
}
.team {
    display: flex;
    align-items: center;  /**/
    padding: 10px 40px;
    position: relative;  /*.frame100%*/
}

/*img.teamNamepositonrelativeindexscale()*/
.team img {
    width: 40px;
    margin-right: 15px;
    position: relative;
    z-index: 9;
}
.teamName {
    position: relative;
    z-index: 9;
}

/**/
.drew {
    opacity: 0.3;
}
/**/
.frame {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #e55451;
    transform-origin: 0;
}

/**/
.frame-enter {
    transform: scaleX(0);
}
.frame-leave-to {
   opacity: 0;
}
.frame-enter-active,
.frame-leave-active {
    transition: all 1s;
}
</style>


