<!-- 
    http://localhost:8080/teams/teamTable/:id/addPlayer 
-->
<!-- $route.query -->
<!-- $route.params -->
<!-- 3-7 currentTeampropsid -->
<!-- 3-7 putplayerId -->
<template>
	<div class="add">
		<h4 class="error-text" v-if="errorText">{{ errorText }}</h4>
		<!-- .prevent -->
		<form v-on:submit.prevent='addPlayer'>
			<fieldset class="player-info">
				<legend></legend>
				<!--  .trim-->
				<div class="info-row">
					<label for="name"></label>
				    <input id="name" type="text" required v-model.trim="addedPlayer.name" 
				        @blur="nameErrorFlag = true">
				    <span class="format-warn" v-if="nameErrorFlag === true">
					    {{ nameError.errorText }}
				    </span>
				</div>
				<!--  .number-->				
				<div class="info-row">
					<label for="number"></label>
				    <input id="number" type="number" required v-model.number="addedPlayer.number">
				</div>
				<!--  -->				
				<div class="info-row">
					<label for="age"></label>
				    <input id="age" type="number" required v-model.number="addedPlayer.age">
				</div>
				<!--  -->				
				<div class="info-row">
					<label for="role"></label>
				    <select id="role" required v-model="addedPlayer.role">
                        <option disabled value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
				    </select>
				</div>
				<!--  -->				
				<div class="info-row">
					<label for="club"></label>
				    <input id="club" type="text" required v-model.trim="addedPlayer.club">
				</div>
				<!--  -->				
				<div class="info-row">
					<label for="birthDate"></label>
				    <input id="birthDate" type="date" v-model="addedPlayer.birthDate">
				</div>
				<!--  -->
				<div class="info-row">
					<label for="height">(cm)</label>
				    <input id="height" type="text" v-model="addedPlayer.height" 
				        @blur="heightErrorFlag=true">
				    <span class="format-warn" v-if="heightErrorFlag === true">
					    {{heightError.errorText }}
					</span>
				</div>
				<!--  -->					
				<div class="info-row">
					<label for="intro"></label>
				    <textarea id="intro" rows="10" cols="50" v-model="addedPlayer.intro"></textarea>
				</div>
				<div class="info-row">
					<label for="button"></label>
				  	<button type="submit"></button>
				    <button type="reset"></button>
			        <router-link 
			    	    class="back"
			    	    :to="`/teams/teamTable/${this.currentId}`">
			    	    &lt;&lt; 
			        </router-link>
			    </div>
			</fieldset>
		</form>
	</div>
</template>

<script>
export default {
	name: 'addPlayer',
	props: ['teams'],
	data() {
		return {
            addedPlayer: {},
            currentId: this.$route.params.id,
            errorText: '',
             // errorflag@blurv-if/text
            nameErrorFlag: false,
            heightErrorFlag: false
		}
	},
	computed: {
        // datacurretnTeamundefined
        // currentTeamput
        currentTeam() {
        	// currentTeamprop
        	// HTML
        	return JSON.parse(JSON.stringify(this.teams[this.currentId]));
        },
        // nameErrorheightError
        nameError() {
        	// 
        	let item = this.addedPlayer.name?this.addedPlayer.name:'';  
        	let reg = /^[a-zA-Z\u4e00-\u9fa5]+(([a-zA-Z\u4e00-\u9fa5]+)?)$/;
        	let text = 'Lionel Messi, Messi, .';
        	return this.inputError(item,reg,text);
        },
        heightError() {
            // 
            let item = this.addedPlayer.height?this.addedPlayer.height:'';
            let reg = /^\d{3}$/;
            let text = '';
            return this.inputError(item,reg,text);
        }
    },

	methods: {
		addPlayer() {
            if(this.nameError.status && this.heightError.status) {
	            // HACK: JSON-server,teamsputkeiyPlyerspost
	            // 
	            let playerId = this.currentTeam.keyPlayers.length;
	            // playerId
	            this.$set(this.addedPlayer, 'playerId', playerId);
	            this.currentTeam.keyPlayers.push(this.addedPlayer);
	            var that = this;
	            this.$emit('add', {'id': that.currentId, 'team': that.currentTeam});
	        }
	        else {
	        	// htmlrequirederrorText
	        	this.errorText = '';
	        }
		},
		// v-modelfilters
		// 
		inputError(item,reg,text) {
            let status =false;
            let errorText = '';
            if(!reg.test(item)) {
            	status = false;
            	errorText = text;
            }
            else {
                status = true;
                errorText = '';
            }
            return {
            	status,
            	errorText
            }
		}
	}
} 
</script>

<style scoped>
.add {
    width: 700px;
	margin: 0 auto;
}
.player-info {
	position: relative;
	padding: 20px 0;
	background: #3d3c3a;
	border: none;
	color: #fff;
}
legend {
	margin-bottom: 10px;
	text-align: center;
	font-size: 16px;
	transform: translateY(70%);
	color: #ff0;
}
.info-row {
	margin-bottom: 15px;
	font: 14px/1.5 "STXiHei";
}
.info-row>label,
.info-row>input,
.info-row>select {
	height: 25px;
	line-height: 25px;
	width: 150px;
	vertical-align: top;
}
.info-row>label {
	width: 100px;
	padding-right: 20px;
	text-align: right;
	color: #ff0;
}
.info-row button {
	width: 50px;
	margin-right: 20px;
	padding: 3px 5px;
	border: none;
	outline-width: 0;
	color: #fff;
}
button[type='submit'] {
	background: #d9534f;
}
button[type='reset'] {
	background: #306eff;
}
.back {
	position: absolute;
	left: 0;
	top: 0;
	padding: 20px 10px;
	font: 14px/1.5 "";
	color: #fff;
}

/**/
.format-warn {
	color: #d9543f;
	padding: 0 10px;
}
</style>


