<style scoped>
#team-area {
    /**/
    display: flex;  
    flex-direction: column;
    justify-content: space-between;
    margin: 0 50px;   /**/
}
/**/
#team-area>h2 {
    font-size: 20px;
    color: #3090c7;
    font-weight: 700;
    border-bottom: 1px solid #ccc;
    text-align: center;
}
/**/
.pots {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-content: space-between;
}
/**/
.individual-pot {
    width: 200px;
    height: 200px;
    margin: 10px 30px 10px 0;
    padding: 5px;
    position: relative;
    border: 1px solid #0020c2;
    border-radius: 20px;
    transition: background-color 1s; /* */
}
.individual-pot>h2 {
    float: left;
    font: 16px/1 "Hiragino GB";
    font-style: italic;
}
.current-pot {
    background-color: #ffebcd;
    box-shadow: 0px 10px 20px 10px #6698ff inset;
}

/**/
.team {
    /**/
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* */
    position: absolute;
    top: 5px;
    left: 50%;
    width: 50px;
    height: 50px;
    transform-origin: center 100px;   /*.individual-pot*/
    /**/
    border: 1px solid #0020c2;
    border-radius: 50%;
    background: #fff;     /**/
    /**/
    overflow: hidden;
    /* v-move*/
    transition: all .3s;    
}
.team-content {
    text-align: center;
}
.team img {
    border: 1px solid #000;
    width: 25px;
    vertical-align: top;
}

/**/
.shuffleBtn {
    position: absolute;
    left: 50%;
    top: 50%;
    padding: 5px 10px;
    transform: translate(-50%,-50%);  /**/
    background-color: #306eff;
    border: 1px solid #ccc;
    border-radius: 5px;
    color: #fff;
    line-height: 2;
    outline-width: 0;
}
.curpot-shuffle:hover {
    background-color: #43bfc7;
}

/**/
.frame {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #c6deff;
    /*background-image: url(require('../../assets/football.jpg)');*/
    background-size: 100% 100%;
    background-position: center;
    cursor: pointer;
    transition: all 1s;   /**/
}
.frame:hover {
    transform: rotate(270deg);
}
.default-frame {
    /*background-image: url(require('../../assets/football-red.jpg'));*/
    background: #f70d1a;
}
.frame-enter,
.frame-leave-to {
	opacity: 0;
}
.frame-enter-active,
.frame-leave-active {
	transition: all 1s;
}
</style>


